/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
}

body {
  padding: 0;
  background-color: #fafafa;
  color: #000000;
  line-height: 1.3;
}

nav {
  height: 5.3125rem;
  width: 100%;
  padding: 1.375rem 2.5rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  color: #696969;
}
@media only screen and (max-width: 999px) {
  nav {
    justify-content: space-between;
    padding: 1.375rem 1.5rem;
  }
}
nav img {
  align-self: center;
}
nav .backMenCont {
  display: inherit;
  width: 100%;
  margin-left: 4rem;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont {
    display: flex;
    width: 60%;
    flex-flow: column nowrap;
    height: 100vh;
  }
}
@media only screen and (max-width: 400px) {
  nav .backMenCont {
    width: 70%;
    flex-grow: 1;
    margin-left: 1rem;
  }
}
nav .backMenCont .menList {
  display: inherit;
  height: 100%;
  align-items: baseline;
}
@media only screen and (min-width: 1000px) {
  nav .backMenCont .menList {
    width: 100%;
  }
}
nav .backMenCont .menList .dropDown {
  height: inherit;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList .dropDown {
    height: auto;
    margin-bottom: 1rem;
  }
}
nav .backMenCont .menList .dropDown > button > .i-features {
  content: url(../../images/icon-arrow-down.svg);
  transition: 0.3s;
}
nav .backMenCont .menList .dropDown > button > .i-company {
  content: url(../../images/icon-arrow-down.svg);
  transition: 0.3s;
}
nav .backMenCont .menList .dropDown > button:focus {
  pointer-events: none;
}
nav .backMenCont .menList .dropDown > button:focus > .i-features {
  content: url(../../images/icon-arrow-down-full.svg);
  transform: rotate(180deg);
}
nav .backMenCont .menList .dropDown > button:focus > .i-company {
  content: url(../../images/icon-arrow-down-full.svg);
  transform: rotate(180deg);
}
nav .backMenCont .menList .dropDown .dropDown-content {
  display: none;
  flex-flow: column nowrap;
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  height: fit-content;
  justify-content: space-between;
  position: absolute;
  box-shadow: 3px 5px 22px 1px lightgray;
  transition-duration: 0.5s;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList .dropDown .dropDown-content {
    position: relative;
    box-shadow: none;
  }
}
nav .backMenCont .menList .dropDown .dropDown-content > a {
  margin: 0.6rem;
}
nav .backMenCont .menList .dropDown .dropDown-content > a > img {
  margin-right: 0.6rem;
}
nav .backMenCont .menList .dropDown .dropDown-content__translatex {
  margin-left: -2.5rem;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList .dropDown .dropDown-content__translatex {
    margin-left: 0;
  }
}
nav .backMenCont .menList .dropDown button:focus + .dropDown-content {
  display: flex;
  animation-name: anim-menu-desk;
  animation-duration: 0.3s;
  animation-timing-function: cubic-bezier(0, 0, 0.37, 1.81);
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList .dropDown button:focus + .dropDown-content {
    animation-name: anim-menu-mobile;
  }
}
nav .backMenCont .menList .dropDown:hover {
  cursor: pointer;
}
nav .backMenCont .menList .dropDown:hover button {
  color: #141414;
}
nav .backMenCont .menList a {
  text-decoration: none;
  color: #696969;
  transition-duration: 0.3s;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList a {
    margin-bottom: 1rem;
  }
}
nav .backMenCont .menList a:hover {
  color: #141414;
}
nav .backMenCont .menList button {
  height: inherit;
  color: #696969;
  transition-duration: 0.3s;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList button {
    height: auto;
    padding: 0;
  }
}
nav .backMenCont .menList button:hover {
  color: #141414;
  cursor: pointer;
}
nav .backMenCont .menList button:last-child {
  padding: 0rem 1rem;
}
nav .backMenCont .menList > .logbtn {
  margin-left: auto;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList > .logbtn {
    align-self: stretch;
    margin: 1rem;
    margin-bottom: 0;
  }
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList > .regbtn {
    align-self: center;
    height: 3rem;
    width: 90%;
    margin: 1rem;
    flex-shrink: 1;
  }
}
nav .backMenCont .menList > * {
  margin-left: 2.5rem;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList > * {
    margin-left: 1rem;
  }
}
@media only screen and (max-width: 999px) {
  nav .backMenCont .menList {
    display: none;
  }
}
nav .backMenCont label {
  cursor: pointer;
  display: flex;
  order: 2;
  margin-left: auto;
  display: none;
  z-index: 1;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont label {
    position: relative;
    margin-top: 0.5rem;
    display: unset;
  }
}
nav .backMenCont input#menu {
  display: none;
  order: 2;
}
@media only screen and (max-width: 999px) {
  nav .backMenCont input#menu:checked ~ .menList {
    display: flex;
    flex-flow: column nowrap;
    background-color: white;
    position: absolute;
    top: 0;
    padding-top: 4rem;
    width: 69%;
    /* autoprefixer: ignore next */
    width: -moz-available;
  }
}
nav .backMenCont input#menu:checked ~ label > img {
  content: url(../../images/icon-close-menu.svg);
}
nav .btn-style-clear {
  border: none;
  background-color: transparent;
}
nav .btn-style-hollow {
  background-color: transparent;
  border: 2px solid #696969;
  border-radius: 0.8rem;
}
nav .btn-style-hollow:hover {
  border: 2px solid #141414;
}

main {
  display: flex;
  justify-content: center;
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 15px;
  max-width: 1200px;
  margin: 0 auto;
}

@keyframes anim-menu-desk {
  from {
    background-color: #E2CD32;
    transform: translateY(-0.5rem);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes anim-menu-mobile {
  from {
    transform: translateY(-0.5rem);
  }
  to {
    transform: translateY(0);
  }
}
* {
  font-family: "Epilogue", sans-serif;
  font-size: 1rem;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
  color: #000000;
}

p {
  font-size: 1.125rem;
}